@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --danger-color :#dc3545;
    --black-color : black;
    --black-lighten: rgb(0, 0, 0, 0.8);
    
}

.orderonlinemain
{
    height: 92.6vh;
    width: 100vw;
    /* background-color: aquamarine; */
   

}

.orderonlinemain ul{
    display: flex;
    flex-direction: column;
    align-items: center;
    /* background-color: beige; */
    list-style-type: none;
    max-width: 70vw;
    row-gap: 10px;
}

.orderonline .logoimg{
   height: 40px;
   width: 150px;
   margin-top: 30px;
   /* background-color: aqua; */
   cursor: pointer;
}
.orderonline p{
  font-size: 16px;
  color: rgb(128, 128, 128, 0.9);
}
.orderonline .selectlocationform{
  width: 200px;
  font-size: 1rem;
  background-color: rgb(76, 76, 77, 0.4);

}
.orderonline .location
{
    font-size: 10px;
}
 
