@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --danger-color :#dc3545;
    --black-color : black;
    --black-lighten: rgb(0, 0, 0, 0.8);
    
}

.contactcontent{
    text-align: center;
   /*  background-image: url('https://wallpapers.com/images/featured/ice-cream-background-gudzr50hbe61ehdb.jpg'); */
   background-image: url('https://www.marbleslab.com/wp-content/uploads/2022/07/join-bg.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 92.6vh;
    width: 100vw;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-weight: 300;
     
    color: rgb(77, 48, 7)
    
}

.contactcontent p, 
.contactcontent h2{
    text-shadow: 1px 1px 2px rgb(45, 36, 22), 0 0 1em rgb(243, 143, 206), 0 0 0.2em rgb(73, 52, 1);
}
 
.cursorpointer{
    cursor: pointer;
}

.home{
    /*  background-image: url('https://wallpapers.com/images/featured/ice-cream-background-gudzr50hbe61ehdb.jpg'); */
   background-image: url('https://wallpaperaccess.com/full/240424.jpg');
    
   background-position: center;
   background-repeat: no-repeat;
   background-size: cover;
   height: 92.6vh;
   width: 100vw;
   display: flex;
   flex-direction: column;
   padding-top: 100px; 
   
    
   color: rgb(77, 26, 7)
}

.white{
    color: var(--black-lighten);
    white-space: nowrap;
    font-size: 8px;
    transition: transform 0.3s ease-in-out;
    font-weight: 500; 
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: aqua; */
   
}

.white:hover{
    color: var(--danger-color);
    transform: scale(1.2);   
    white-space: nowrap;
   

}
 

.mainnavbar{
 min-height: 40px;
 box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
 /* background-color: blue; */
 width: 100vw;
 display: flex;
 align-items: center;
 /* justify-content: center; */
}

.navbarcontainer
{
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: green; */
    width: fit-content;
}
.colgap{
     column-gap: 20px;
     /* margin-top: 10px; */
      /* background-color: aqua; */
}


.home h4{
    font-family: 'Roboto', sans-serif;
    font-size: 1.2rem;
    letter-spacing: 1px;
    text-align: left; 
    max-width: 30vw;
    /* background-color: aqua; */
    margin-left: 60px;
    color: rgba(58, 55, 55, 0.8);
    
}

.mainnavbarlogo{
    
    width: 100px;
}

