@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --danger-color :#dc3545;
    --black-color : black;
    --black-lighten: rgb(0, 0, 0, 0.8);
    
}

.img-parent{
    width: 100vw;
    height: 92.6vh;
    position: relative;
}
.wallpaper-img
{
    height: 100%;
    width: 100%;
}

.about-content-bg{
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    background-color: rgb(128, 128, 128, 0.3);
    background-color: transparent;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 30px;
}

.about-content-bg p{
    max-width: 600px;
    font-size: 8px;
    font-weight: 100;
    text-align: center;
    font-family: 'Roboto', sans-serif;
    color: var(--black-lighten );
    padding: 0 50px;
   max-width: 80vw;
    
}
.about-content-bg p:last-child{
    font-size: 7px;
     
}


@media screen and (min-width: 575.98px) 
 { 
    
   .about-content-bg p{
     font-size: 10px;  
     max-width: 70vw;
   }
   .about-content-bg p:last-child{
    font-size: 9px;
    /* background-color: rgb(189, 175, 174); */
   }
}

 
@media screen and (min-width: 767.98px) { 
     
       .about-content-bg p{
         font-size: 12px;
         max-width: 60vw;
       }
       .about-content-bg p:last-child{
        font-size: 10px;
        /* background-color: rgb(241, 74, 56); */
       }
    
 }

 
@media screen and (min-width: 991.98px) { 
    
        
    .about-content-bg p{
        font-size: 12px;
     }
     .about-content-bg p:last-child{
      font-size: 10px;
      /* background-color: rgb(121, 87, 245); */
     }
 }

 
@media screen and (min-width: 1199.98px) { 
    .about-content-bg p{
        font-size: 14px;
     }
     .about-content-bg p:last-child{
      font-size: 12px;
      /* background-color: rgb(237, 87, 245); */
     }
 }

 
@media screen and (min-width: 1399.98px) { 
     
  
