@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
 
:root{
    --danger-color :#dc3545;
    --black-color : black;
    --black-lighten: rgb(0, 0, 0, 0.6);
    
}

.products{
    height: 88vh;
    /* background-color: aquamarine; */
      
}

.products ul {
    /* background-color: blanchedalmond; */
    display: flex;
    padding: 0;
    list-style-type: none;
    height: 100%;
    height: 92.6vh;
    
}

.products li{
    /* background-color: rgb(141, 101, 190);  */
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    
    
}

.products li img{
    height:80vh;
    width: 250px;  
   
}

.products li:hover{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; 
     
}  

 

.product{
    
    /* background-color: rebeccapurple; */
    position: relative;
    z-index: -1;
}

.product p{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
   /* background-color: blueviolet; */
   transition: transform 0.5s linear;
    font-weight: 600;
    color: rgb(0, 0, 0, 0.3);
}

.products li:hover p{
    background-color: rgb(255, 255, 255, 0.8);
    color: var(--black-lighten);
    transform: scale(1.1);
}  

